import React, { useEffect, useState } from "react";
import {
  createBrowserRouter,
  RouterProvider,
  Navigate,
  createHashRouter,
} from "react-router-dom";
import jwt_decode from "jwt-decode";
import { Toaster } from "react-hot-toast";
import Home from "../src/compnents/Home/Home.jsx";
import Projects from "../src/compnents/Projects/Projects.jsx";
import Skills from "../src/compnents/Skills/Skills.jsx";
import Contact from "../src/compnents/Contact/Contact.jsx";
import Layout from "../src/compnents/Layout/Layout.jsx";

import Notfound from "./compnents/Notfound/Notfound.jsx";
import Background from "./compnents/Background/Background.js";
export default function App() {
  let routers = createHashRouter([
    {
      path: "",
      element: <Layout />,
      children: [
        {
          index: true,
          element: <Home />,
        },
        {
          path: "skills",
          element: <Skills />,
        },
        {
          path: "Projects",
          element: <Projects />,
        },
        {
          path: "Contact",
          element: <Contact />,
        },
        {
          path: "*",
          element: <Background />,
        },
      ],
    },
  ]);
  return <RouterProvider router={routers} />;
}
