import React from "react";
import Navbar from "../Navbar/Navbar";
import Bottombar from "../Bottombar/Bottombar";
import { Outlet } from "react-router-dom";

export default function Layout() {
  return (
    <>
      <Bottombar />
      <div className="page">
        <Navbar></Navbar>
        <Outlet></Outlet>
      </div>
    </>
  );
}
