import React from "react";
import { NavLink } from "react-router-dom";
import "./Navbar.css";
export default function Navbar() {
  function openTap() {
    let sideBar = document.getElementById("navSide");
    sideBar.classList.toggle("changeWidth");
    document.getElementById("openTap").classList.replace("d-flex", "d-none");

    document.getElementById("closeTap").classList.replace("d-none", "d-flex");
  }

  function closeTap() {
    let sideBar = document.getElementById("navSide");
    sideBar.classList.toggle("changeWidth");
    document.getElementById("openTap").classList.replace("d-none", "d-flex");

    document.getElementById("closeTap").classList.replace("d-flex", "d-none");
  }
  return (
    <>
      <div className="sidebar" id="navSide">
        <div className="close-mun my-2 position-relative"></div>
        <div className="black-box my-4">
          <h3 className="d-flex align-items-baseline m-auto">
            <span
              className="char-stay"
              style={{ color: "#080f46", textShadow: "-3px 1px 0px #b10067" }}
            >
              A
            </span>
            <h3 className="sidebar-text">hmed</h3>
          </h3>
        </div>
        <div className="taps my-5">
          <ul>
            <li>
              <NavLink
                className={({ isActive }) => (isActive ? "active" : "")}
                to="/"
              >
                <i className="fa-solid fa-user"></i>
                <span>Portfolio </span>
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/skills"
                className={({ isActive }) => (isActive ? "active" : "")}
              >
                <i className="fa-solid fa-pen"></i>
                <span>Skills </span>
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/projects"
                className={({ isActive }) => (isActive ? "active" : "")}
              >
                <i className="fa-brands fa-r-project"></i>
                <span>Projects</span>
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/contact"
                className={({ isActive }) => (isActive ? "active" : "")}
              >
                <i className="fa-solid fa-phone-volume"></i>
                <span>Contact</span>
              </NavLink>
            </li>
          </ul>
        </div>
        <div className="social-account">
          <NavLink
            to="https://www.facebook.com/profile.php?id=100051282400888"
            target="_blank"
          >
            <div className="icons" style={{ color: "#0d8af0" }}>
              <i className="fa-brands fa-facebook"></i>
            </div>
          </NavLink>
          <NavLink
            href="https://www.linkedin.com/in/ahmed-nasser-669456248/"
            target="_blank"
          >
            <div className="icons" style={{ color: "#0a66c2" }}>
              <i className="fa-brands fa-linkedin"></i>
            </div>
          </NavLink>
          <NavLink href="https://github.com/Ahmed-Nasser99" target="_blank">
            <div className="icons" style={{ color: "black" }}>
              <i className="fa-brands fa-github"></i>
            </div>
          </NavLink>
          <NavLink
            href="https://www.instagram.com/a7mednasser___/?igshid=YmMyMTA2M2Y%3D"
            target="_blank"
          >
            <div className="icons">
              <i className="fa-brands fa-instagram insta instagramIcon"></i>
            </div>
          </NavLink>
        </div>
      </div>
      <button onClick={closeTap} id="closeTap" className="d-none">
        <i className="fa-solid fa-xmark"></i>
      </button>
      <button onClick={openTap} id="openTap" className="d-flex">
        <i className="fa-solid fa-bars"></i>
      </button>
    </>
  );
}
