import React, { useEffect, useState } from "react";
import { Link, NavLink } from "react-router-dom";
import $ from "jquery";
import "./Bottombar.css";
export default function Bottombar() {
  let [Leftborder, setLeftborder] = useState("2px");
  function activeBorder(e) {
    setLeftborder(`${$(e.target).offset().left - 50}px`);
  }
  useEffect(() => {}, []);
  return (
    <>
      <nav id="nav-phone" className="d-none">
        <menu className="menu">
          <NavLink
            onClick={activeBorder}
            to="/"
            className={({ isActive }) =>
              isActive ? "menu__item active" : "menu__item"
            }
            style={{ background: "#ff8c00" }}
          >
            <div className="d-flex icon">
              <i className="fa-solid fa-user"></i>
            </div>
          </NavLink>
          <NavLink
            onClick={activeBorder}
            to="/skills"
            className={({ isActive }) =>
              isActive ? "menu__item active" : "menu__item"
            }
            style={{ background: "#f54888" }}
          >
            <div className="icon">
              <i className="fa-solid fa-pen"></i>
            </div>
          </NavLink>
          <NavLink
            onClick={activeBorder}
            to="/projects"
            className={({ isActive }) =>
              isActive ? "menu__item active" : "menu__item"
            }
            style={{ background: "#4343f5" }}
          >
            <div className="icon">
              <i className="fa-brands fa-r-project"></i>
            </div>
          </NavLink>
          <NavLink
            onClick={activeBorder}
            to="/contact"
            className={({ isActive }) =>
              isActive ? "menu__item active" : "menu__item"
            }
            style={{ background: "#e0b115" }}
          >
            <div className="icon">
              <i className="fa-solid fa-phone-volume"></i>
            </div>
          </NavLink>

          <div className="menu__border" style={{ left: Leftborder }}></div>
        </menu>
        <div className="svg-container">
          <svg viewBox="0 0 202.9 45.5">
            <clipPath
              id="menu"
              clipPathUnits="objectBoundingBox"
              transform="scale(0.0049285362247413 0.021978021978022)"
            >
              <path
                d="M6.7,45.5c5.7,0.1,14.1-0.4,23.3-4c5.7-2.3,9.9-5,18.1-10.5c10.7-7.1,11.8-9.2,20.6-14.3c5-2.9,9.2-5.2,15.2-7
                  c7.1-2.1,13.3-2.3,17.6-2.1c4.2-0.2,10.5,0.1,17.6,2.1c6.1,1.8,10.2,4.1,15.2,7c8.8,5,9.9,7.1,20.6,14.3c8.3,5.5,12.4,8.2,18.1,10.5
                  c9.2,3.6,17.6,4.2,23.3,4H6.7z"
              />
            </clipPath>
          </svg>
        </div>
      </nav>
    </>
  );
}
