    const templateParams = {
      values,
    };

    emailjs
      .send(
        "<service_u0sagiv>",
        "<template_spioq8l>",
        templateParams,
        "<Kyr1oHd39qUc8iH96>"
      )
      .then(
        (response) => {
          console.log("SUCCESS!", response.status, response.text);
        },
        (err) => {
          console.log("FAILED...", err);
        }
      );
